body{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    background-color: teal;
    font-family: 'Josefin Sans', sans-serif;
}

.main_div{
    width: 100%;
    height: auto;
    display: flex;
    justify-content: center;
    font-family: 'Josefin Sans', sans-serif;
}

.todo{
    width: 22%;
    height: auto;
    margin-top: 20px;
    padding: 20px 50px;
    background-color: lightgray;
    border: 3px solid black;
    border-radius: 20px;
    font-family: 'Josefin Sans', sans-serif;
}

h1{
    padding: 15px 0;
    background-color: teal;
    border-radius: 20px;
    text-align: center;
    border: 3px solid black;
    text-decoration: underline;
}

input{
    background-color: transparent;
    padding: 5px 0px;
    font-size: 18px;
    border: none;
    border-bottom: 2px solid black;
    text-align: center;
    font-weight: bold;
    font-family: 'Josefin Sans', sans-serif;
    text-transform: capitalize;
}

input:focus{
    outline: none;
}

.list{
    padding-left: 0px;
    text-align: left;
    font-size: 24px;
    font-weight: bold;
    list-style: none;
    min-height: 40px;
    display: flex;
    align-items: center;
    text-transform: capitalize;
}

.fa-plus{
    margin-left: 20px;
    background-color: black;
    color: white;
    border: 3px solid black;
    border-radius: 100%;
    padding: 10px 10px;
}

.fa-plus:hover{
    background-color:green;
    color: black;
    cursor: pointer;
}

.fa-trash{
    width: 20px;
    height: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 15px 0 10px;
    border-radius: 50%;
    border: 3px solid darkolivegreen;
    background-color: black;
    padding: 10px 10px;
    color: white;
    cursor: pointer;
}

.fa-trash:hover{
    color: red;
}

@media(max-width: 800px){
    .todo{
        width: 65%;
    }

    input{
        width: 65%;
    }

}